let fs = 14;
let lh = 1.5;
let rlh = fs * lh;
let ts = {
  textTransform: 'uppercase',
};

let Key = item => {
  let [key, label] = item;
  return (
    <div
      style={{
        marginRight: '1ch',
        display: 'flex',
        marginBottom: rlh / 4,
      }}
    >
      <div
        style={{
          border: 'solid 1px black',
          paddingLeft: '0.5ch',
          paddingRight: '0.5ch',
        }}
      >
        {key}
      </div>
      <div
        style={{
          paddingLeft: '0.5ch',
          paddingRight: '0.5ch',
          borderTop: 'solid 1px #ddd',
          borderRight: 'solid 1px #ddd',
          borderBottom: 'solid 1px #ddd',
          background: '#ddd',
        }}
      >
        {label}
      </div>
    </div>
  );
};

let KeyList = ({ items }) => {
  return (
    <div style={{ display: 'flex' }}>
      {items.map(item => {
        return Key(item);
      })}
    </div>
  );
};

let Relevant = ({ mode }) => {
  switch (mode) {
    case 'move':
      return (
        <div>
          <div style={ts}>Move</div>
          <KeyList items={[['h', '←'], ['j', '↓'], ['k', '↑'], ['l', '→']]} />
          <KeyList items={[['shift', 'hold to move by 10']]} />
          <div style={ts}>Action</div>
          <KeyList items={[['enter', 'select flow'], ['2', 'adjust cursor']]} />
        </div>
      );
    default:
      return null;
  }
};

export default ({ rlh }) => {
  return (
    <div
      style={{
        border: 'solid 1px black',
        paddingRight: '1ch',
        paddingLeft: '1ch',
        paddingTop: rlh / 4,
        paddingBottom: rlh / 4,
      }}
    >
      <Relevant mode="move" />
      <div style={ts}>Special</div>
      <KeyList items={[['a', 'west'], ['w', 'north']]} />
    </div>
  );
};
